function pluralize (time, label) {
  return time + label + ''
}

export function timeAgo (time) {
  const between = (+Date.now() - +new Date(time)) / 1000
  if (between < 60) {
    return pluralize(~~(between), ' ')
  } else if (between < 3600) {
    return pluralize(~~(between / 60), ' ')
  } else if (between < 86400) {
    return pluralize(~~(between / 3600), ' ')
  } else if (between < (86400 * 30)) {
    return pluralize(~~(between / 86400), ' ')
  } else if (between < (86400 * 30 * 12)) {
    return pluralize(~~(between / (86400 * 30)), '')
  } else {
    return pluralize(~~(between / (86400 * 30 * 12)), '')
  }
}

export function formatDate (date, fmt) {
  date = new Date(date)
  fmt = fmt || 'yyyy-MM-dd hh:mm'
  let o = {
    'M+': date.getMonth() + 1, // 
    'd+': date.getDate(), // 
    'h+': date.getHours(), // 
    'm+': date.getMinutes(), // 
    's+': date.getSeconds(), // 
    'q+': Math.floor((date.getMonth() + 3) / 3), // 
    'S': date.getMilliseconds() // 
  }
  if (/(y+)/.test(fmt)) {
    fmt = fmt.replace(RegExp.$1, (date.getFullYear() + '').substr(4 - RegExp.$1.length))
  }
  for (let k in o) {
    if (new RegExp('(' + k + ')').test(fmt)) {
      fmt = fmt.replace(RegExp.$1, (RegExp.$1.length === 1) ? (o[k]) : (('00' + o[k]).substr(('' + o[k]).length)))
    }
  }
  return fmt
}

export function translateTab (tab, isTop) {
  if (isTop) {
    return ''
  } else {
    switch (tab) {
      case 'share':
        return ''
      case 'ask':
        return ''
      case 'job':
        return ''
      case 'good':
        return ''
      case 'top':
        return ''
    }
  }
}



